import React, { Component } from 'react';
import './MainView.css';
import { faMusic, faPause, faEllipsisH, faHeart, faHorseHead } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export class MainView extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isPlay: true,
            activeIndex: '',
            albumData: '',
            isChoseTrack: ''
        }
    }

    convertTime(millis) {
        var minutes = Math.floor(millis / 60000);
        var seconds = ((millis % 60000) / 1000).toFixed(0);
        return minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
    }

    toggle = () => {
        this.setState({
            isPlay: !this.state.isPlay,
            activeIndex: 0
        });
    }

    handleClick(index, track) {
        this.setState({
            activeIndex: index,
            isPlay: true
        })
        console.log(track);
    }

    render() {
        if (this.state.albumData) {
            console.log(this.state.albumData);
            return (
                <div className="MainView">
                    <div className="row">
                        <div className="Summary col-xs-12 col-lg-3 col-xl-4">
                            <div className="Cover">
                                <img src={this.state.albumData.images[0].url} alt="cover-small" />
                            </div>
                            <div className="Title">
                                <span>{this.state.albumData.name}</span>
                            </div>
                            <div className="Artist">
                                <span>{this.state.albumData.artists[0].name}</span>
                            </div>
                            <div className="Action">
                                <button className="btn btn-secondary" onClick={this.toggle}>
                                    {this.state.isPlay ? "Play" : "Stop"}
                                </button>
                            </div>
                            <div className="Info">
                                <span>{this.state.albumData.release_date}</span>
                                <span><FontAwesomeIcon icon={faHorseHead} /></span>
                                <span>{this.state.albumData.total_tracks} Songs</span>
                            </div>
                            <div className="Action">
                                <span><FontAwesomeIcon icon={faHeart} /></span>
                                <span><FontAwesomeIcon icon={faEllipsisH} /></span>
                            </div>
                        </div>
                        <ol className="TrackList col-xs-12 col-lg-9 col-xl-8">
                            {this.state.albumData.tracks.items.map((track, index) => {
                                const className = this.state.activeIndex === index ? 'activeTrack row' : 'normalTrack row';
                                return (
                                    <li className={className} key={index} onClick={() => this.handleClick(index, track)}>
                                        <div className="col-xs-2 col-lg-2 col-xl-2 center">
                                            {this.state.activeIndex === index ?
                                                <FontAwesomeIcon icon={faPause} />
                                                :
                                                <FontAwesomeIcon icon={faMusic} />
                                            }
                                        </div>
                                        <div className="col-xs-6 col-lg-6 col-xl-6">
                                            {track.name}
                                        </div>
                                        <div className="col-xs-2 col-lg-2 col-xl-2 center">
                                            <FontAwesomeIcon icon={faEllipsisH} />
                                        </div>
                                        <div className="col-xs-2 col-lg-2 col-xl-2 center">
                                            {track.duration_ms}
                                        </div>
                                    </li>
                                )
                            })}
                        </ol>
                    </div>
                </div>
            )
        }

        return (
            <div className="MainView">
                <h2>NoData</h2>
            </div>
        )

    }

    componentDidMount() {
        const token = this.props.accessToken;
        const url = `https://api.spotify.com/v1/albums/${this.props.match.params.id}`;
        fetch(url, {
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Authorization': `Bearer ${token}`,
            }
        })
            .then(response => response.json())
            .then(data => {
                var newType = data.tracks.items;
                newType.map((item) => {
                    return item.duration_ms = this.convertTime(item.duration_ms);
                })
                this.setState({ albumData: data });
            })
            .catch(e => {
                window.location.replace(`https://accounts.spotify.com/authorize?client_id=e2a42674970c41b6b7b24c6144da9d1d&response_type=token&Foauth_callback%2F&scope=streaming user-read-playback-state user-read-currently-playing user-modify-playback-state&redirect_uri=https:%2F%2Fcb0510.github.io/spotifake_demo/album/${this.props.match.params.id}`);
                console.log('error', e);
            });
    }
}

export default MainView