import React, { Component } from 'react';
import { NavLink } from "react-router-dom";
import './NavBar.css';
import logo from '../images/spotify-icons-logos/icons/01_RGB/02_PNG/Spotify_Icon_RGB_Black.png';
import homeIcon from '../images/home.png';
import search from '../images/search.png';
import library from '../images/library.png';
import download from '../images/download.png';

class NavBar extends Component {

    constructor(props) {
        super(props);
        this.state = {
            userData: {
                recentlyPlayed: []
            }
        }
    }

    fetchUser() {
        fetch('https://hailduong.github.io/lyduong/react-assignment/spotify/apis/userInfo.json')
            .then(response => response.json())
            .then(data => {
                var newType = data.recentlyPlayed;
                newType.map((item, index) => {
                    return item.type = this.convertType(item.type);
                })
                this.setState({ userData: data });
            })
            .catch(e => console.log('error', e));
    }

    convertType(typeNumber) {
        switch (typeNumber) {
            case "1": return "Album";
            case "2": return "Playlist"
            default: return "Unknown"
        }
    }

    render() {
        return (
            <nav className="NavBar">
                <div className="NavHeader">
                    <img src={logo} alt="logo"/>
                    <span>Spotifake</span>
                </div>
                <ul className="NavContent" key="menu">
                    <li key="menu-1" className="NavBarGroup">
                        <NavLink to="/home" activeClassName="activeLink">
                            <div className='NavItem'>
                                <div className='NavItemIcon'>
                                    <img src={homeIcon} alt='home' />
                                </div>
                                <span className="">Home</span>
                            </div>
                        </NavLink>
                    </li>
                    <li key="menu-2" className="NavBarGroup">
                        <NavLink to="/search" activeClassName="activeLink">
                            <div className='NavItem'>
                                <div className='NavItemIcon'>
                                    <img src={search} alt='search' />
                                </div>
                                <span>Search</span>
                            </div>
                        </NavLink>
                    </li>
                    <li key="menu-3" className="NavBarGroup">
                        <NavLink to="/library" activeClassName="activeLink">
                            <div className='NavItem'>
                                <div className='NavItemIcon'>
                                    <img src={library} alt='library' />
                                </div>
                                <span>Your Library</span>
                            </div>
                        </NavLink>
                    </li>
                </ul>
                <div className="RecentlyPlayed">
                    <span>RECENTLY PLAYED</span>
                    <hr />
                    {this.state.userData.recentlyPlayed.map((item, index) => {
                        return (
                            <div className='RecentlyPlayedItem' key={index}>
                                <a href="#top">
                                    <span>{item.name}</span>
                                </a>
                                <br />
                                <span>{item.type}</span>
                            </div>
                        )
                    })}
                </div>
                <div className="NavFooter">
                    <a href="#top">
                        <div className="Action">
                            <img src={download} alt="download" />
                            <span>Install App</span>
                        </div>
                    </a>
                    <hr />
                    <a href="#top">
                        <div className="SessionInfo">
                            <img className="Avatar" src={this.state.userData.avatar} alt="avatar" />
                            <span>{this.state.userData.name}</span>
                        </div>
                    </a>
                </div>
            </nav>
        );
    }

    componentDidMount() {
        this.fetchUser();
    }
}

export default NavBar