import React from 'react';
import { faMusic, faEllipsisH } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import './SearchResult.css';
import noimage from '../../images/noimage.png';

const SearchResult = props => {
    if (props.handleClick) {
        const handleClick = props.handleClick;
        console.log(typeof (handleClick));
    }
    if (props.tracks.length > 0 || props.albums.length > 0 || props.artists.length > 0 || props.playlists.length > 0) {
        const tracks = props.tracks;
        const albums = props.albums;
        const artists = props.artists;
        const playlists = props.playlists;

        return (
            <div className='search-result'>
                <hr />
                <h3>Tracks</h3>
                <ol className="search-tracks">
                    {tracks.map((track, index) => {
                        const className = 'normalTrack row';
                        return (
                            <li className={className} key={index} onClick={() => this.handleClick(index, track)}>
                                <div className="col-xs-2 col-lg-2 col-xl-2 center">
                                    <FontAwesomeIcon icon={faMusic} />
                                </div>
                                <div className="col-xs-6 col-lg-6 col-xl-6 trackTitle">
                                    {track.name}
                                </div>
                                <div className="col-xs-2 col-lg-2 col-xl-2 center">
                                    <FontAwesomeIcon icon={faEllipsisH} />
                                </div>
                                <div className="col-xs-2 col-lg-2 col-xl-2 center">
                                    {track.duration_ms}
                                </div>
                            </li>
                        )
                    })}
                </ol>
                <hr />
                <h3>Albums</h3>
                <div className='row'>
                    {albums.map((album, index) => {
                        const className = 'album col-2';
                        return (
                            <div className={className} key={index}>
                                <img src={album.images[1].url} alt={album.name} />
                                <hr />
                                <span>{album.name}</span>
                            </div>
                        );
                    })}
                </div>
                <hr />
                <h3>Artists</h3>
                <div className='row'>
                    {artists.map((artist, index) => {
                        const className = 'album col-2';
                        return (
                            <div className={className} key={index}>
                                {
                                    artist.images.length > 0 
                                    ? <img src={artist.images[0].url} alt={artist.name}/>
                                    : <img src={noimage} alt="noimage"/>
                                }
                                <hr />
                                <span>{artist.name}</span>
                            </div>
                        );
                    })}
                </div>
                <hr />
                <h3>Playlists</h3>
                <div className='row'>
                    {playlists.map((playlist, index) => {
                        const className = 'album col-2';
                        return (
                            <div className={className} key={index}>
                                {
                                    playlist.images.length > 0 
                                    ? <img src={playlist.images[0].url} alt={playlist.name}/>
                                    : <img src={noimage} alt="noimage"/>
                                }
                                <hr />
                                <span>{playlist.name}</span>
                            </div>
                        );
                    })}
                </div>
            </div>
        );
    } else {
        return (
            <h3>No Result</h3>
        );
    }
}

export default SearchResult