import React, { Component } from 'react';
import { Link } from "react-router-dom";
import './Library.css';

class Library extends Component {
    constructor(props) {
        super(props);
        this.state = {
            libraryData: [],
        }
    }

    convertTime(millis) {
        var minutes = Math.floor(millis / 60000);
        var seconds = ((millis % 60000) / 1000).toFixed(0);
        return minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
    }

    fetchLibrary() {
        const token = this.props.accessToken;
        const url = `https://api.spotify.com/v1/albums?ids=41MnTivkwTO3UUJ8DrqEJJ%2C6JWc4iAiJ9FjyK0B59ABb4%2C6UXCm6bOO4gFlDQZV5yL37`;
        fetch(url, {
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Authorization': `Bearer ${token}`,
            }
        })
            .then(response => response.json())
            .then(data => {
                this.setState({ libraryData: data.albums });
            })
            .catch(e => {
                window.location.replace("https://accounts.spotify.com/authorize?client_id=e2a42674970c41b6b7b24c6144da9d1d&response_type=token&Foauth_callback%2F&scope=streaming user-read-playback-state user-read-currently-playing user-modify-playback-state&redirect_uri=https:%2F%2Fcb0510.github.io/spotifake_demo/library");
                console.log('error', e);
            });
    }

    render() {
        if(this.state.libraryData){
            return (
                <div className='album'>
                    <h1>Albums</h1>
                    <div className='row'>
                        {
                            this.state.libraryData.map((item, index) => {
                                return (
                                    <div key={item.id} className='col-md-4 col-lg-2'>
                                        <Link to={"/album/" + (item.id)} >
                                            <div className="card">
                                                <img className="card-img-top" src={item.images[0].url} alt="Cover" />
                                                <div className="card-body">
                                                    <h6 className="card-title">{item.name}</h6>
                                                </div>
                                            </div>
                                        </Link>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
            )
        }
        return(
            <div className="album">
                <h3>Loading...</h3>
            </div>
        )
    }

    componentDidMount() {
        this.fetchLibrary();
    }
}

export default Library;