import React from 'react';
import coverSmall from '../images/cover-small.jpeg';
import './NowPlayingBar.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHeart, faRandom, faStepBackward, faStepForward, faPlay, faRetweet, faListAlt, faDesktop, faVolumeUp } from '@fortawesome/free-solid-svg-icons';

const NowPlayingBar = () => {
    return (
        <div className="NowPlayingBar">
            <div className="row">
                <div className="NowPlayingBar-Left col-lg-4">
                    <div className="row">
                        <div className="TrackImage col-lg-4">
                            <img src={coverSmall} alt="track-img" />
                        </div>
                        <div className="TrackInfo col-lg-6">
                            <a href="#top">Song Title</a>
                            <br />
                            <a href="#top">Artist</a>
                        </div>
                        <div className="TrackAction col-lg-2">
                            <FontAwesomeIcon icon={faHeart} />
                        </div>
                    </div>
                </div>
                <div className="NowPlayingBar-Center col-lg-4">
                    <div className="row middle">
                        <div className="col-12">
                            <div className='row'>
                                <div className='col-2 center'><FontAwesomeIcon icon={faRandom} /></div>
                                <div className='col-2 center'><FontAwesomeIcon icon={faStepBackward} /></div>
                                <div className='col-4 center'><FontAwesomeIcon icon={faPlay} /></div>
                                <div className='col-2 center'><FontAwesomeIcon icon={faStepForward} /></div>
                                <div className='col-2 center'><FontAwesomeIcon icon={faRetweet} /></div>
                            </div>
                        </div>
                        <hr />
                        <div className="col-2 center">
                            <span>0:30</span>
                        </div>
                        <div className="col-8">
                            <div className="progress">
                                <div className="progress-bar progress-bar-striped progress-bar-animated bg-secondary w-50" role="progressbar" aria-valuenow="50" aria-valuemin="0" aria-valuemax="100"></div>
                            </div>
                        </div>
                        <div className="col-2 center">
                            <span>1:00</span>
                        </div>
                    </div>
                </div>
                <div className="NowPlayingBar-Right col-lg-4">
                    <div className='row middle'>
                        <div className='col-2'><FontAwesomeIcon icon={faListAlt} /></div>
                        <div className='col-2'><FontAwesomeIcon icon={faDesktop} /></div>
                        <div className='col-2'><FontAwesomeIcon icon={faVolumeUp} /></div>
                        <div className='col-6'>
                            <div className="progress">
                                <div className="progress-bar bg-secondary w-50" role="progressbar" aria-valuenow="50" aria-valuemin="0" aria-valuemax="100"></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default NowPlayingBar