import React, { Component } from 'react';
import { BrowserRouter as Switch, Route } from 'react-router-dom';
import './App.css';
import NavBar from './components/NavBar';
import NowPlayingBar from './components/NowPlayingBar';
import MainView from './components/MainView';
import Library from './components/MainView/Library'
import Home from './components/MainView/Home';
import Search from './components/MainView/Search';
import SpotifyWebApi from 'spotify-web-api-js';
const spotifyApi = new SpotifyWebApi();

class App extends Component {
  constructor(props) {
    super(props);
    //get hashParam for access token
    const params = this.getHashParams();
    //get access token
    const token = params.access_token;
    if (token) {
      spotifyApi.setAccessToken(token);
      this.state = {
        album: '',
        token: token,
        isPlay: '',
      };
    }
    else {
      this.state = {
        album: '',
        token: '',
        isPlay: '',
      };
    }
  }

  //get hashParam for access token
  getHashParams() {
    var hashParams = {};
    var e, r = /([^&;=]+)=?([^&;]*)/g,
      q = window.location.hash.substring(1);
    e = r.exec(q)
    while (e) {
      hashParams[e[1]] = decodeURIComponent(e[2]);
      e = r.exec(q);
    }
    return hashParams;
  }

  getTrack(dataFromChild) {
    this.setState({
      isPlay: dataFromChild
    })
  }

  render() {
    return (
      <Switch>
        <div className="row">
          <div className='col-xs-12 col-md-3 col-lg-3 col-xl-3'>
            <NavBar />
          </div>
          <div className='col-xs-12 col-md-9 col-lg-9 col-xl-9'>
            <Route exact path='/' component={Home} />
            <Route path='/home' component={Home} />
            <Route path='/search' render={() => <Search accessToken={this.state.token} />} />
            <Route path='/library' component={Library} accessToken={this.state.token} action={this.getTrack} />
            <Route path="/album/:id" component={(props) => <MainView {...props} getTrack={this.getTrack} accessToken={this.state.token} />} />
          </div>
          <div className="col-12">
            <NowPlayingBar inputVal={this.state.isPlay} />
          </div>
        </div>
      </Switch>
    );
  }

  componentDidMount() {

  }
}

export default App;