import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch } from '@fortawesome/free-solid-svg-icons';
import SearchResult from './SearchResult';

class Search extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: '',
            tracks: '',
            albums: '',
            artists: '',
            playlists: '',
            activeTrack: '',
            token: ''
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleClick = this.handleClick.bind(this);
    }

    convertTime(millis) {
        var minutes = Math.floor(millis / 60000);
        var seconds = ((millis % 60000) / 1000).toFixed(0);
        return minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
    }

    handleClick(track) {
        this.props.action(track);
    }

    handleChange(event) {
        this.setState({ value: event.target.value });
    }

    handleSubmit(event) {
        const token = this.props.accessToken;
        const url = `https://api.spotify.com/v1/search?q=${this.state.value}&type=track,album,artist,playlist&limit=10`;
        fetch(url, {
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'Authorization': `Bearer ${token}`,
            }
        })
            .then(response => response.json())
            .then(data => {
                var newType = data.tracks.items;
                newType.map((item) => {
                    return item.duration_ms = this.convertTime(item.duration_ms);
                })
                this.setState({
                    tracks: data.tracks.items,
                    albums: data.albums.items,
                    artists: data.artists.items,
                    playlists: data.playlists.items,
                });
            })
            .catch(e => {
                window.location.replace("https://accounts.spotify.com/authorize?client_id=e2a42674970c41b6b7b24c6144da9d1d&response_type=token&Foauth_callback%2F&scope=streaming user-read-playback-state user-read-currently-playing user-modify-playback-state&redirect_uri=https:%2F%2Fcb0510.github.io/spotifake_demo/search");
                console.log('error', e);
            });
        event.preventDefault();
    }

    render() {
        return (
            <div className="center">
                <h1>Search Spotifake</h1>
                <form onSubmit={this.handleSubmit}>
                    <div className='input-group mb-3'>
                        <input type='text' placeholder="enter track, arrtist, album or playlist ..."
                            className='form-control'
                            value={this.state.value} onChange={this.handleChange}
                        />
                        <div className='input-group-append'>
                            <button type='submit' className='btn btn-secondary'>
                                <FontAwesomeIcon icon={faSearch} />
                            </button>
                        </div>
                    </div>
                </form>
                <SearchResult
                    tracks={this.state.tracks}
                    albums={this.state.albums}
                    artists={this.state.artists}
                    playlists={this.state.playlists}
                    action={this.handleClick}
                />
            </div>
        );
    }
    componentDidMount() {

    }
}

export default Search;